:root {
	--PRIMARY_BLUE: #1E314F;
	--PRIMARY_BLUE_SHADOW: #325185;
	--PRIMARY_FONT_COLOR: #000c66;
	--SECONDARY_FONT_COLOR: #348eb8;
	/* --PRIMARY_BLUE: #0a1932; */
}

* {
	padding: 0;
	margin: 0;
	text-decoration: none;
	box-sizing: border-box;
	/* list-style: none; */
	/* font-family: 'Montserrat', sans-serif; */
	/* font-family: 'Caveat', cursive; */
	font-family: 'Inconsolata', monospace;
	scroll-behavior: smooth;
}

body {
	/* background: linear-gradient(to right, #021d44, #0e2f5e); */
	/* background-color: #142440; */
	/* color: white; */
	/* background: linear-gradient(to right, #021d44, #0e2f5e); */
	background-color: #E4F9F5;
	color: var(--PRIMARY_FONT_COLOR);
	min-height: 100vh;
	position: relative;
	padding-bottom: 50px;
}

.intro {
	display: flex;
	justify-content: center;
	align-items: center;
}

.introText {
	font-size: xx-large;
	margin-top: 20px;
	text-align: center;
}

.percentageBar {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 30px 10px;
	text-align: center;
	font-size: small;
}

.progress {
	height: 10px;
	background-color: var(--PRIMARY_BLUE_SHADOW);
	border-radius: 2px;
}

/* Heading */
.header {
	font-weight: bolder;
	text-decoration: underline;
	/* font-family: 'Permanent Marker', cursive; */
}

/* Bullets */
.bullet-list {
	list-style-type: disc !important;
	padding-left: 20px;
}

.textInput {
	outline: none;
	border: none;
	border-bottom: 2px solid var(--SECONDARY_FONT_COLOR);
	/* border-bottom: 2px solid #4ab9ec; */
	background: none;
	color: var(--SECONDARY_FONT_COLOR);
	padding: 5px;
	width: 450px;
	height: 50px;
}

textarea.textInput {
	height: 100px;
}

.customBtn {
	width: 450px;
	color: white;
	font-weight: bolder;
	background: var(--SECONDARY_FONT_COLOR);
	/* background: #4ab9ec; */
	outline: none;
	border: none;
	padding: 5px;
	border-radius: 2px;
}

@media (max-width: 480px) {
	#about {
		margin-top: 50px;
	}

	.fst :nth-child(5) {
		margin-bottom: 0;
	}

	.textInput,
	.customBtn {
		width: 100%;
	}

	.percentageBar {
		margin: 30px 0;
	}
}

#contact form {
	text-align: center;
}

ol,
ul {
	padding-left: 0;
}

.scrollPos {
	padding: 2px;
	background-color: #348eb8;
	transition: 0.3s ease;
	width: 0;
}